.App {
  height: 100%;
  background-image: linear-gradient(#93d3f3b3 0%, #62a5dbb3 50%, #ffffff 100%);
}

.App > h1 {
  text-align: center;
  text-shadow: 3px 3px 0 hsl(180, 100%, 50%);
  margin-bottom: 5rem;
}

.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10%;
  margin-top: 5rem;
}

.btn {
  cursor: pointer;
  appearance: none;
  padding: 1em;
  border: none;
  outline: 0.2em solid #000000;
  border-radius: 5rem;
  background-color: lightslategray;
  transition: background 300ms linear;
}

.btn:nth-child(1) {
  justify-self: end;
}

.btn:nth-child(2) {
  justify-self: start;
}

.btn:hover {
  background-color: #00ffff;
}

.compContainer {
  grid-auto-rows: 350px;
  margin-top: 5rem;
}

.items {
  width: 90%;
  border-radius: 3rem;
  padding: 2rem;
  border: 0.15rem solid #000000;
  text-align: center;
  line-height: 1.5;
}

.items:nth-child(1) {
  justify-self: end;
}

.items:nth-child(2) {
  justify-self: start;
}

.items > h1 {
  margin-bottom: 2rem;
}

.items > p + p {
  margin-top: 1rem;
}

@media screen and (max-width: 550px) {
  .container {
    grid-template-columns: 1fr;
    gap: 10%;
    margin-top: 3rem;
  }

  .btn:nth-child(1) {
    justify-self: center;
  }

  .btn:nth-child(2) {
    justify-self: center;
  }

  .Comp:nth-child(1) {
    justify-self: center;
  }

  .Comp:nth-child(2) {
    justify-self: center;
  }
}
